import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { BannerService } from 'src/app/service/banner.service';
declare var swal: any;

@Component({
  selector: 'app-banner-add',
  templateUrl: './banner-add.component.html'
})
export class BannerAddComponent implements OnInit {

  loadingState = false;
  addForm: FormGroup;
  selectedFile: File;
  editImageUrl: any = null;

  showLoader = false;
  formErrors = {
    title: null,
    apierror: null
  };
  isEditing = false;
  editId = null;

  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private fb: FormBuilder,
    private bannerservice: BannerService,
  ) {
    if (this.activatedRoute.snapshot.paramMap.get('id')) {
      this.isEditing = true;
      this.editId = this.activatedRoute.snapshot.paramMap.get('id');
      this.editObject();
    }
    this.addForm = fb.group({
      title: [null, Validators.compose([Validators.required])],
      url: [null, Validators.compose([Validators.required])],
      info: [''],
      is_active: [true, Validators.compose([Validators.required])],
    });
  }

  ngOnInit() {
  }


  editObject() {
    this.loadingState = true;
    this.bannerservice.getBannerById(this.editId).subscribe((response) => {
      this.loadingState = false;
      if (response.data) {
        this.editImageUrl = response.data.image,
          this.addForm.patchValue({
            title: response.data.title,
            info: response.data.info,
            is_active: response.data.is_active,
            url: response.data.url,
          });
      }
    }, (error) => {
      this.loadingState = false;
      this.router.navigateByUrl('banner');
    });
  }


  fileChangeEvent(fileInput: any) {
    const reg = /(.*?)\.(jpg|jpeg|png|gif|giff)$/;
    if (!fileInput.target.files[0].name.match(reg)) {
      swal.fire(
        'Invalid File!',
        'Please select valid file ',
        'error'
      );
      this.removeFile();
      return false;
    } else {
      this.removeFile();
      this.selectedFile = fileInput.target.files[0];
    }
  }

  removeFile() {
    this.selectedFile = null;
  }
  submitForm(formdata): void {
    if (this.addForm.valid) {
      this.showLoader = true;
      const formData = new FormData();
      formData.append('url', formdata.value.url);
      formData.append('title', formdata.value.title);
      formData.append('info', formdata.value.info);
      formData.append('is_active', formdata.value.is_active);
      if (this.selectedFile) {
        formData.append('file', this.selectedFile);
      }
      if (this.isEditing) {
        this.bannerservice.editBanner(this.editId, formData).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.router.navigateByUrl('banner');
          } else {
            this.showLoader = false;
            response.error.map(obj => {

              if (obj.hasOwnProperty('title')) {
                this.formErrors.title = obj.title;
              }
            });
          }
        });
      } else {
        this.bannerservice.addBanner(formData).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.router.navigateByUrl('banner');
          } else {
            this.showLoader = false;
            response.error.map(obj => {
              if (obj.hasOwnProperty('title')) {
                this.formErrors.title = obj.title;
              }

            });
          }
        });
      }
    }
  }
}

